<template>
  <div>
    <div class="content">
      <input v-model="keyword" type="text" placeholder="输入你要搜索的城市">
    </div>
    <!-- 只有有关键词的情况,就让search-content显示 -->
    <div ref="wrapper" class="search-content" v-show="keyword">
      <ul>
        <li
          @click="handleClick"
          class="item"
          v-for="(item,index) of list"
          :key="index"
        >{{item.name}}</li>
        <!-- 搜索不到内容,就是list.length为0的情况,就让一下显示 -->
        <li class="item" v-show="!list.length">没有你想要的城市</li>
      </ul>
    </div>
  </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  name: "CitySearch",
  props: ["cities"],
  data() {
    return {
      keyword: "",
      /* 接收哪些能够搜索到的数据 */
      list: []
    };
  },
  watch: {
    keyword() {
      if (!this.keyword) {
        this.list = [];
        return;
      }
      var result = [];
      for (var i in this.cities) {
        this.cities[i].forEach(ele => {
          if (
            ele.spell.startsWith(this.keyword) ||
            ele.name.startsWith(this.keyword)
          ) {
            result.push(ele);
          }
        });
      }
      this.list = result;
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.scroll = new BScroll(this.$refs.wrapper, {});
    });
  },
  methods: {
    handleClick(event) {
      this.$store.commit("changeCity", event.target.innerText);
      this.$router.push("/");
      this.keyword = ""
    }
  }
};
</script>

<style lang="scss" scoped>
@import "style/global.scss";
.content {
  position: fixed;
  width: 100%;
  top: 89px;
  z-index: 999;
  height: 88px;
  display: flex;
  background: color(bg);
  align-items: center;
  justify-content: center;
  input {
    border: 1px solid #eee;
    border-radius: 15px;
    outline: none;
    font-size: 14px;
    padding-left: 20px;
    height: 66px;
    width: 92%;
  }
}
.search-content {
  position: absolute;
  top: 177px;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 60;
  background: #fff;
  overflow: hidden;
}
// .wrapper{
//     height: 1400px;
// }
.item {
  line-height: 60px;
  padding-left: 20px;
  border-bottom: 1px solid #ccc;
}
</style>
